import { useState } from 'react';
import styled from 'styled-components';
import styles from '../css/Test.module.css';

export const ModalContainer = styled.div`
  // Modal    CSS 
  display : flex;
  justify-content : center;
  align-items : center;
  height : 100%;
`;

export const ModalBackdrop = styled.div`
  // Modal     CSS 
  z-index: 1; // 
  position: fixed;
  display : flex;
  justify-content : center;
  align-items : center;
  background-color: rgba(0,0,0,0.4);
  border-radius: 10px;
  top : 0;
  left : 0;
  right : 0;
  bottom : 0;
`;

export const ModalBtn = styled.button`
  background-color: var(--coz-purple-600);
  text-decoration: none;
  border: none;
  padding: 20px;
  color: white;
  border-radius: 30px;
  cursor: grab;
`;

export const ExitBtn = styled(ModalBtn) `
background-color : #4000c7;
border-radius: 10px;
text-decoration: none;
margin: 10px;
padding: 5px 10px;
width: 40px;
height: 40px;
display : flex;
justify-content : center;
align-items : center;
`;

export const ModalView = styled.div.attrs((props) => ({
  // attrs     div     .
  role: 'dialog',
}))`
  // Modal CSS .
  display: flex;
  align-items: center;
  flex-direction: column;
  border-radius: 20px;
  width: 500px;
  heigth: 200px;
  background-color: yellow;
    >div.desc {
      margin: 50px;
      font-size: 20px;
      color: var(--coz-purple-600);
    }
`;

const Test = () => {
  const [isOpen, setIsOpen] = useState(false);

  const openModalHandler = () => {
    // isOpen    
    // !false -> !true -> !false
    setIsOpen(!isOpen) 
  };

  return (
    <>
      <ModalContainer>
        <ModalBtn onClick={openModalHandler}
        //  Modal  (isOpen) boolean     . 
        > Open Modal
          {/*    Modal  (isOpen true )  ModalBtn   'Opened!'  Modal  (isOpen false )  ModalBtn    'Open Modal'   */}
        </ModalBtn>
        {/*    Modal  (isOpen true )        */}
        {isOpen ? 
        <ModalBackdrop onClick={openModalHandler}>
            <ModalView onClick={(e) => e.stopPropagation()}>
              <ExitBtn onClick={openModalHandler}>x</ExitBtn>
              <div className={styles.desc}>HELLO FEJIGU!</div>
            </ModalView>
          </ModalBackdrop>
          : null
        }
      </ModalContainer>
    </>
  );
};

export default Test;